body {
  display: grid;
  min-height: 100vh;
  place-content: center;
}
.Pages {
  width: 100vw;
  height: 100vh;
  display: flex;
  overflow: auto;
  scroll-snap-type: both mandatory;
}
.Selector {
  display: flex;
  place-content: center;
}
.Reason {
  position: absolute;
  top: 0;
  left: 0;
}
.Setting {
  position: absolute;
  top: 0;
  right: 0;
  height: 48px;
  padding: 10px 20px;
  opacity: 0.33;
}
.Box {
  scroll-snap-align: center;
  width: 100vw;
  flex-shrink: 0;
  display: grid;
  place-content: center;
  font-size: 100px;
  font-weight: 900;
  color: var(--fg);
  background: var(--bg);
  box-shadow: inset 0 0 150px #0009;
  text-shadow: 0 0 40px var(--fg);
}
